class IMEOptions {
  static rime_url: string = "http://127.0.0.1:12345";

  setSchema = async (schema: string = "") => {
    let body = "=schema";
    if (schema != "") {
      body += "=" + schema;
    }

    const res = await fetch(IMEOptions.rime_url, { method: "POST", body });
    const text = await res.text();

    return {
      success: text === schema,
      msg: text
    };
  }

  getSchema = async () => {
    let body = "=schema";

    const res = await fetch(IMEOptions.rime_url, { method: "POST", body });
    const text = await res.text();

    return text;
  }

  private postAndUpdateFuzzy = async (body: string) => {
    const res = await fetch(IMEOptions.rime_url, { method: "POST", body });
    let text = await res.text();
    console.log("fuzzy text", text)
    return text.split("\n");
  }

  getFuzzy = async () => {
    let body = `=algebra`;
    return await this.postAndUpdateFuzzy(body);
  }

  addFuzzy = async (option: string) => {
    let body = `=algebra+${option}`;
    return await this.postAndUpdateFuzzy(body);
  }

  removeFuzzy = async (option: string) => {
    let body = `=algebra-${option}`;
    return await this.postAndUpdateFuzzy(body);
  }
}

export default new IMEOptions();
