import React from "react";
import IMEOptions from "@/utils/options";

import { FormControl, FormControlLabel, Radio, RadioGroup, Checkbox, FormGroup, TextField, Snackbar } from "@mui/material";

import styles from "./styles.less";

import Animation from "@/utils/animation";

const schemaMap = [
  {
    value: "luna_pinyin_simp",
    label: ""
  },
  {
    value: "double_pinyin_flypy",
    label: ""
  },
  {
    value: "double_pinyin_mspy",
    label: ""
  },
  {
    value: "luna_pinyin",
    label: ""
  },
  {
    value: "double_pinyin",
    label: ""
  },
  {
    value: "cangjie5",
    label: ""
  },
  {
    value: "cangjie3",
    label: ""
  },
];

const fuzzyMap = [
  {
    value: "derive/^([zcs])h/$1/",
    label: "zh, ch, sh => z, c, s"
  },
  {
    value: "derive/^([zcs])([^h])/$1h$2/",
    label: "z, c, s => zh, ch, sh"
  },
  {
    value: "derive/^n/l/",
    label: "n => l"
  },
  {
    value: "derive/^l/n/",
    label: "l => n"
  },
  {
    value: "derive/([ei])n$/$1ng/",
    label: "en => eng, in => ing"
  },
  {
    value: "derive/([ei])ng$/$1n/",
    label: "eng => en, ing => in"
  }
];

interface IHomeProps {
}

interface IHomeState {
  currentSchema: string;
  currentFuzzy: Array<string>;
  snackbarOpen: boolean;
  snackbarText: string;
  pageSize: string;
}

class Home extends React.Component<IHomeProps, IHomeState>  {
  constructor(props: IHomeProps) {
    super(props);
    this.state = {
      currentSchema: "",
      currentFuzzy: [],
      snackbarOpen: false,
      snackbarText: "",
      pageSize: "",
    };
  }

  componentDidMount = async () => {
    // @ts-ignore
    window.IMEOptions = IMEOptions;
    const currentSchema = await IMEOptions.getSchema();
    const currentFuzzy = await IMEOptions.getFuzzy();
    const pageSize = localStorage.getItem("pageSize") || "8";

    if (currentSchema && currentFuzzy) {
      this.setState({
        currentSchema,
        currentFuzzy,
        pageSize
      });
    }
  }

  actionChangeSchema = async (schema: string) => {
    const res = await IMEOptions.setSchema(schema);
    if (res.success) {
      localStorage.setItem("schema", res.msg);
      localStorage.setItem("schema_change", "true");

      const currentFuzzy = await IMEOptions.getFuzzy();
      if (currentFuzzy) {
        this.setState({
          currentSchema: res.msg,
          currentFuzzy,
        });
        return;
      }
    }

    this.setState({
      snackbarOpen: true,
      snackbarText: "",
    });
  }

  actionChangeFuzzy = async (type: boolean, fuzzy: string) => {
    const res = type ? await IMEOptions.addFuzzy(fuzzy) : await IMEOptions.removeFuzzy(fuzzy);
    this.setState({
      currentFuzzy: res
    });
  }

  actionChangePageSize = (pageSize: string) => {
    this.setState({
      pageSize
    });
    localStorage.setItem("pageSize", pageSize)
  }

  public render() {
    const { currentSchema, currentFuzzy, snackbarOpen, snackbarText, pageSize } = this.state;

    return <div className={styles.content}>
      <div className={styles.bgBlock}>
        <div className={styles.leftTop1} />
        <div className={styles.leftTop2} />
        <div className={styles.leftTop3} />
        <div className={styles.rightMid1} />
        <div className={styles.rightMid2} />
      </div>
      <div className={styles.topAnimation}>
        <Animation
          loop={true}
          width={500}
          height={180}
        />
      </div>
      <div className={styles.formGroup}>
        <div className={styles.formBox}>
          <FormControl className={styles.formControl}>
            <div className={styles.formLabel}></div>
            <FormGroup>
              <RadioGroup
                value={currentSchema}
                onChange={async (e) => await this.actionChangeSchema(e.target.value)}
                name="schema"
                row
              >
                {
                  schemaMap.map((schema) =>
                    <FormControlLabel
                      control={<Radio />}
                      value={schema.value}
                      label={schema.label}
                    />
                  )
                }
              </RadioGroup>
            </FormGroup>
          </FormControl>
        </div>
        <div className={styles.formBox}>
          <FormControl className={styles.formControl}>
            <div className={styles.pageSize}>
              <div className={styles.formLabel}></div>
              <TextField
                className={styles.input}
                id="outlined-basic"
                variant="outlined"
                value={pageSize}
                onChange={e => {
                  if (e.target.value.match(/^\+?[1-9]\d*$/) || e.target.value === "") {
                    this.actionChangePageSize(e.target.value);
                  } else {
                    this.setState({
                      snackbarOpen: true,
                      snackbarText: " 3-10 ",
                    });
                  }
                }}
                onBlur={e => {
                  if (!e.target.value.match(/^([3|4|5|6|7|8|9]|(10))$/)) {
                    this.actionChangePageSize("10");
                    this.setState({
                      snackbarOpen: true,
                      snackbarText: " 3-10 ",
                    });
                  }
                }}
              />
            </div>
          </FormControl>
        </div>
        {
          ["luna_pinyin_simp", "luna_pinyin"].includes(currentSchema) &&
          <div className={styles.formBox}>
            <FormControl className={styles.formControl}>
              <div className={styles.formLabel}></div>
              <FormGroup row>
                {
                  fuzzyMap.map((fuzzy) =>
                    <FormControlLabel
                      control={
                        <Checkbox
                          value={fuzzy.value}
                          name={fuzzy.label}
                          checked={currentFuzzy.includes(fuzzy.value)}
                          onChange={async (e) => await this.actionChangeFuzzy(e.target.checked, e.target.value)}
                        />
                      }
                      label={fuzzy.label}
                      key={fuzzy.value}
                    />
                  )
                }
              </FormGroup>
            </FormControl>
          </div>
        }
      </div>
      <div className={styles.footer}>FydeOS is made possible by gentle souls with real </div>
      <Snackbar
        anchorOrigin={{
          vertical: 'top',
          horizontal: 'center',
        }}
        open={snackbarOpen}
        autoHideDuration={3000}
        ContentProps={{
          'aria-describedby': 'message-id',
        }}
        onClose={() => this.setState({ snackbarOpen: false, snackbarText: "" })}
        message={<span id="message-id">{snackbarText}</span>}
      />
    </div>
  }
}

// @ts-ignore
export default Home;
